* {
  margin: 0;
  padding: 0;
}

.jumprope-container {
  max-width: 991px;
  width: 100%;
  background: #fff;
  margin: 0 auto;
}

.jumprope-pic {
  display: block;
  font-size: 0;
  width: 100%;
}

.jumprope-pic-part2 {
  margin-top: 15%;
}

.jumprope-word {
  font-size: 14px;
  line-height: 1.78;
}

.jumprope-part {
  position: relative;
  z-index: 1;
}

.jumprope-content {
  padding-left: 30px;
  padding-right: 30px;
}

.jumprope-part1 {
  z-index: 2;
}

.jumprope-part1-top {
  font-size: 14px;
  padding-top: 5px;
  padding-bottom: 5px;
  background: #000;
  color: #fff;
  line-height: 1.38;
  text-align: center;
}

.jumprope-nav {
  position: relative;
  width: 100%;
  background: #fff;
  box-sizing: border-box;
  box-shadow: 0 0px 3px 0 #0000fa;
}

.jumprope-nav-word {
  font-size: 14px;
  padding-right: 60px;
  padding-top: 12px;
  padding-bottom: 12px;
  font-weight: bold;
}

.jumprope-nav-btn {
  display: block;
  width: 55px;
  height: 26px;
  background: url("https://cdn.shopify.com/s/files/1/0519/2148/6021/files/jumprope_pic-btn-buy.png?v=1622168961");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: absolute;
  right: 30px;
  top: 50%;
  margin-top: -13px;
}

.jumprope-nav-fixed {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 2;
}

.jumprope-part2-desc {
  position: absolute;
  left: 0;
  top: 28.8%;
  color: #fff;
}

.jumprope-part3 {
  padding-top: 12%;
  padding-bottom: 8%;
}

.jumprope-part5 {
  padding-top: 12%;
  padding-bottom: 12%;
}

.jumprope-part5 .jumprope-word {
  margin-top: 10%;
}

.jumprope-list {
  padding-top: 10%;
  padding-bottom: 12%;
}

.jumprope-list-item {
  font-size: 14px;
  list-style: none;
  position: relative;
  line-height: 1.48;
  margin-bottom: 3px;
}

.jumprope-list-item::before {
  content: '';
  width: 4px;
  height: 4px;
  background: #c199f1;
  border-radius: 100%;
  position: absolute;
  left: -10px;
  top: 8px;
}

.jumprope-params {
  padding-top: 10%;
  padding-bottom: 12%;
}

.jumprope-params-list {
  padding: 10% 0 8% 0;
}

.jumprope-params-item {
  font-size: 0;
  list-style: none;
  padding: 4% 0 4% 0;
  border-bottom: 1px #c3c3c3 solid;
}

.jumprope-params-label {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  font-size: 15px;
  font-weight: bold;
}

.jumprope-params-desc {
  width: 50%;
  display: inline-block;
  vertical-align: top;
  font-size: 15px;
  text-align: right;
}

.jumprope-footer {
  padding-bottom: 12%;
}

.jumprope-footer-title {
  font-size: 14px;
  margin-bottom: 5%;
  font-weight: bold;
}

.jumprope-footer-btn {
  margin-bottom: 2%;
}

.jumprope-footer-link {
  display: inline-block;
}

.jumprope-footer-link .jumprope-pic {
  width: 92px;
  margin-right: 10px;
}

.jumprope-footer-link .jumprope-pic-health {
  margin-top: 15%;
}

.jumprope-footer-desc {
  font-size: 12px;
}

.jumprope-swiper {
  margin-top: 5%;
}

.jumprope-swiper-desc {
  font-size: 14px;
  font-weight: lighter;
  margin-top: 10%;
  line-height: 1.78;
  margin-bottom: 5%;
}

@media (max-width: 340px) {
  .jumprope-content {
    padding-left: 15px;
    padding-right: 15px;
  }
  .jumprope-word {
    line-height: 1.58;
  }
}

@media (min-width: 550px) {
  .jumprope-word {
    font-size: 20px;
  }
  .jumprope-list-item {
    font-size: 20px;
  }
  .jumprope-params-desc, .jumprope-params-label {
    font-size: 20px;
  }
  .jumprope-footer-title {
    font-size: 20px;
  }
  .jumprope-footer-desc {
    font-size: 18px;
  }
}
